.p44-color-picker-wrapper {
  position: relative;
  width: 115px;
  height: 36px;
  cursor: pointer;
  background-color: white;
  border: 1px solid #d5d4d4;
  display: flex;
  align-items: center;
  box-sizing: content-box; }
  .p44-color-picker-wrapper .p44-color-picker {
    height: 24px;
    width: 115px;
    display: block;
    position: relative; }
  .p44-color-picker-wrapper .p44-color-picker::before {
    position: absolute;
    background-color: white;
    left: 0;
    top: 0;
    width: 6px;
    height: 24px;
    content: ""; }
  .p44-color-picker-wrapper .p44-color-picker::after {
    position: absolute;
    background-color: white;
    right: 0;
    top: 0;
    width: 28px;
    height: 24px;
    content: ""; }
  .p44-color-picker-wrapper .p44-chevron-down-icon {
    position: absolute;
    right: 2px;
    width: 24px;
    height: 24px; }

.rc-color-picker .rc-color-picker-panel-preview {
  border-radius: 50%; }
  .rc-color-picker .rc-color-picker-panel-preview span {
    border-radius: 50%; }

.rc-color-picker .rc-color-picker-panel-params-input input, .rc-color-picker .rc-color-picker-panel-params-input input:focus {
  border: 1px solid #cacaca; }
