.radio-group.block {
  display: flex;
  background-color: #f3f3f3;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1); }
  .radio-group.block > label {
    flex: 1;
    text-align: center;
    color: #aba9a8;
    background-color: transparent !important; }
    .radio-group.block > label.ant-radio-button-wrapper-checked {
      background-color: #e3ebf1 !important;
      border-color: #d5d4d4 !important;
      color: #575451 !important; }

.radio-group.styled {
  color: #575451;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase; }
  .radio-group.styled .ant-radio-button-wrapper {
    height: auto !important; }
    .radio-group.styled .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
      background-color: #e3ebf1;
      border-color: inherit;
      color: #575451;
      box-shadow: none; }
      .radio-group.styled .ant-radio-button-wrapper.ant-radio-button-wrapper-checked span:nth-child(2) {
        opacity: 1; }
    .radio-group.styled .ant-radio-button-wrapper:hover {
      color: #575451; }
      .radio-group.styled .ant-radio-button-wrapper:hover span:nth-child(2) {
        opacity: 1; }
    .radio-group.styled .ant-radio-button-wrapper span:nth-child(2) {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
      padding: 6px 0;
      opacity: .5;
      transition: all 150ms linear; }
    .radio-group.styled .ant-radio-button-wrapper .item-icon {
      height: 15px;
      position: relative;
      margin-top: 8px;
      z-index: 1; }
    .radio-group.styled .ant-radio-button-wrapper .item-title {
      display: block;
      align-self: center;
      font-size: 12px;
      font-weight: bold;
      line-height: 1.25;
      text-transform: uppercase;
      color: #575451;
      z-index: 1;
      position: relative;
      margin-top: 4px; }
