.ant-calendar-input {
  font-size: 12px; }

.ant-calendar-date:hover {
  border-radius: 50%; }

.ant-calendar-today:not(.ant-calendar-disabled-cell) .ant-calendar-date {
  border-radius: 50%; }

.ant-calendar-selected-day:not(.ant-calendar-disabled-cell) .ant-calendar-date {
  background: #236192;
  font-weight: bold;
  color: #FFF;
  border-radius: 50%;
  border-color: #236192; }
  .ant-calendar-selected-day:not(.ant-calendar-disabled-cell) .ant-calendar-date:hover {
    border-radius: 50%; }

.ant-calendar .ant-calendar-footer-btn .ant-calendar-today-btn {
  color: #2091a9;
  text-transform: uppercase;
  font-weight: bold; }

.ant-calendar-picker .ant-calendar-picker-input {
  font-size: 12px; }
