.sticky-nav {
  width: 68px;
  position: fixed;
  left: 0;
  top: 200px;
  border-radius: 0;
  transition-duration: 0.3s;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  background-color: var(--white);
  z-index: 1;
  cursor: pointer; }
  @media (max-width: 1199px) {
    .sticky-nav {
      width: 54px;
      left: -12px; } }
  .sticky-nav:hover {
    width: 242px; }
    .sticky-nav:hover a {
      text-decoration: none; }
  .sticky-nav__item {
    height: 60px;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: bold;
    color: #9b9b9b;
    padding: 0px 18px !important;
    display: flex;
    overflow: hidden; }
    .sticky-nav__item:hover {
      background-color: #ebebeb; }
    .sticky-nav__item a {
      display: flex;
      align-items: center;
      text-decoration: none; }
    .sticky-nav__item span {
      color: #9b9b9b;
      font-size: 14px;
      padding-left: 30px;
      white-space: nowrap; }
    .sticky-nav__item i {
      color: #9b9b9b; }
    .sticky-nav__item svg g {
      fill: #9b9b9b; }
