.chips .chips__row {
  flex-direction: row;
  flex-wrap: wrap;
  display: flex;
  margin-top: 12px; }

.chips .column {
  flex-direction: column;
  display: flex; }

.chips .chip {
  display: flex;
  background-color: #aba9a8;
  border-radius: 0px;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 2px 0px;
  margin: 0 3px 5px 0;
  align-self: flex-start; }
  .chips .chip__label {
    font-size: 11px;
    line-height: 24px;
    color: #FFFFFF;
    padding: 0 12px;
    font-weight: 400; }
  .chips .chip__delete {
    color: #FFFFFF;
    cursor: pointer;
    font-size: 16px;
    line-height: 24px;
    padding: 0 5px; }

.chips__show-all {
  padding: 10px 7px;
  display: inline-block;
  cursor: pointer; }
  .chips__show-all .text_action {
    display: flex;
    font-size: 10px;
    align-items: center; }
